.block{display:flex;align-items:center;justify-content:center;background-color:rgba(14,181,83,.5);border-radius:23px;width:46px;height:46px;position:fixed;bottom:40px;z-index:5;cursor:pointer;margin-right:calc(-1*(100vw - 100%));transition:all .3s;opacity:0;visibility:hidden}.block_active{transition:all .3s;opacity:1;visibility:visible}.block:hover{background-color:rgba(14,181,83,.7);transition:all .3s}